<template>
        <div class='warp'>
            <div class='top'>
               <p  @click='back'>全部车款</p>
            </div>
            <section>
                <div class='type_box'>
                    <span @click="changeYear(item)" v-for="(item, index) in years" :key="index" :class="currentYear==item?'active':''">{{item}}</span>
                  </div>
                  <div class='list_box'>
                      <ul v-for='(item,index) in lists' :key='index'> 
                          <h6>{{index}}</h6>
                          <li v-for='(val,key) in item' :key='key' class='box'>
                              <p>{{val.market_attribute.year+'款 '+val.car_name}}</p>
                              <span>{{`${val.max_power}马力${val.gear_num}档${val.trans_type}`}}</span>
                              <p class='price_box'>
                                    <span>指导价{{`${val.market_attribute.dealer_price_min}`}}</span>
                                    <span class='color'>{{val.market_attribute.official_refer_price}}</span>
                              </p>
                              <p class='float_box'>询问底价</p>
                          </li>
                           
                      </ul>
                  </div>
            </section>
        </div>
    </template>
<script>
    import {
        mapState,
        mapMutations,
        mapActions,
        mapGetters
    } from 'vuex'
    export default {
        computed: {
            ...mapState({
                // detaiList: state => state.detail.detaiList,
                currentYear: state => state.detail.currentYear
            }),
            ...mapGetters({
                years: 'detail/years',
                lists: 'detail/lists'
            })
        },
        methods: {

            ...mapMutations({
                changeYear: 'detail/changeYear'
            }),
            ...mapActions('detail', ['DatailLists']),
            updata() {
                let id = this.$route.query.id;
                this.DatailLists(id).then(res => {
                    console.log(id)
                })
            },
            back() {
                this.$router.back()
            }
        },
        mounted() {
            this.updata();
            //console.log(this.DatailLists())
            console.log(this.$route.query.id)

        },
    }
</script>
<style scoped lang='scss'>
    .warp {
        width: 100%;
        height: 100%;
        background: #eee;
        display: flex;
        flex-direction: column;
    }
    
    .top {
        background: #fff;
        width: 100%;
        height: .8rem;
        -webkit-box-align: center;
        align-items: center;
        box-sizing: border-box;
        z-index: 99;
        display: flex;
        justify-content: space-around;
        p {
            font-size: .28rem;
            color: #00afff
        }
    }
    
    section {
        width: 100%;
        height: 100%;
        .type_box {
            width: 100%;
            height: 1rem;
            margin-top: .2rem;
            background: #fff;
            line-height: .5rem;
            span {
                font-size: .28rem;
                padding: 0 .3rem;
            }
        }
        .list_box {
            width: 100%;
            height: 6rem;
            ul {
                h6 {
                    font-size: .36rem;
                    font-weight: 400;
                    padding: .1rem;
                    color: #999;
                }
                .box {
                    line-height: 2;
                    font-size: 0;
                    background: #fff;
                    position: relative;
                    p {
                        font-size: .36rem;
                        padding: 0 .1rem;
                        color: #3d3d3d;
                    }
                    span {
                        font-size: .28rem;
                        color: #bdbdbd;
                        padding: 0 .1rem .1rem .1rem;
                    }
                    .price_box {
                        text-align: right;
                        border-bottom: 1px solid #ccc;
                        span {
                            color: #818181;
                        }
                        .color {
                            color: red
                        }
                    }
                    .float_box {
                        text-align: center;
                        color: #00afff
                    }
                    .bootom_box {
                        text-align: center;
                        color: #00afff;
                    }
                }
            }
        }
    }
</style>


